import React, {useContext} from 'react'
import {Link} from 'react-router-dom'
import {CartContext} from '../Global/CartContext'

const Navbar = () => {
    const {qty} = useContext(CartContext);
    return (
        <nav>
            <ul className="left">
                <li><Link to="/">MT-Store</Link></li>
            </ul>
            <ul className="right">
                <li><Link to="cart"><span className="shopping-cart"><i className="fas fa-cart-plus"></i></span></Link><span className="cart-count">{qty}</span></li>
            </ul>
        </nav>
    )
}

export default Navbar
