import React, {createContext, useState} from 'react'

import dslr from '../assets/dslr.jpg'
import headphone from '../assets/headphone.jpg'
import iphone from '../assets/iphone.jpg'
import microphone from '../assets/microphone.jpg'
import perfume from '../assets/perfume.jpg'
import ring from '../assets/ring.jpg'
import shoe from '../assets/shoe.jpg'
import watch from '../assets/watch.jpg'

export const ProductsContext = createContext();

const ProductsContextProvider = (props) => {
    const [products] = useState([
        {id: 1, name:"DSLR", price:80, image:dslr, status:"HOT"},
        {id: 2, name:"Head-Phones", price:50, image:headphone, status:"NEW"},
        {id: 3, name:"iPhones", price:800, image:iphone, status:"HOT"},
        {id: 4, name:"Microphones", price:40, image:microphone, status:"NEW"},
        {id: 5, name:"Perfumes", price:8, image:perfume, status:"HOT"},
        {id: 6, name:"Rings", price:15, image:ring, status:"NEW"},
        {id: 7, name:"Shoes", price:12, image:shoe, status:"NEW"},
        {id: 8, name:"Watches", price:10, image:watch, status:"HOT"}
    ]);
    return (
        <ProductsContext.Provider value={{products: [...products]}}>
            {props.children}
        </ProductsContext.Provider>
    )
}

export default ProductsContextProvider;